import React, { useState, useEffect, useMemo } from "react";
import PaymentService from "../services/PaymentService";
import Slider from "@mui/material/Slider";
import { usePlaidLink } from "react-plaid-link";
import {
  FormGroup,
  FormLabel,
  FormControl,
  FormControlLabel,
  Checkbox,
  Radio,
  RadioGroup,
  Button,
} from "@mui/material";

const PlaidLink = ({ token }) => {
  const { open, ready } = usePlaidLink({
    token,
    onSuccess: (public_token, metadata) => {
      // send public_token to server
    },
  });

  useEffect(() => {
    if (ready) {
      open();
    }
  }, [ready, open]);

  return <></>;
};

const TOPPINGS_MAP = {
  'pearls': {
    key: "pearls",
    label: "Pearls (+0.20)",
    price: 20,
  },
  'jelly':{
    key: "jelly",
    label: "Jelly (+0.20)",
    price: 20,
  },
  'aloe_vera':{
    key: "aloe_vera",
    label: "Aloe Vera (+0.30)",
    price: 30,
  },
  'ice_cream':{
    key: "ice_cream",
    label: "Ice Cream (+0.50)",
    price: 50,
  },
  'mystery_topping':{
    key: "mystery_topping",
    label: "Mystery Topping (+0.50)",
    price: 50,
  },
};

const SIZES_MAP = {
  'small':{
    key: "small",
    label: "Small (1.00)",
    price: 100,
  },
  'large':{
    key: "large",
    label: "Large (1.50)",
    price: 150,
  },
};

const levels = [
  {
    value: 0,
    label: '0%',
  },
  {
    value: 25,
    label: '25%',
  },
  {
    value: 50,
    label: '50%',
  },
  {
    value: 75,
    label: '75%',
  },
  {
    value: 100,
    label: '100%',
  },
];

const Main = (props) => {
  const [token, setToken] = useState("");
  const [sugar, setSugar] = useState(50);
  const [ice, setIce] = useState(50);
  const [size, setSize] = useState("small");
  const [toppings, setToppings] = useState({
    pearls: false,
    jelly: false,
    aloe_vera: false,
    ice_cream: false,
    mystery_topping: false,
  });
  const buy = async () => {
    console.log("buy");
    const data = await PaymentService.createPayment(price);
    console.log(data);
    setToken(data.link_token);
  };

  const selectToppings = (key) => {
    setToppings({
      ...toppings,
      [key]: !toppings[key]
    });
  };

  const price = useMemo(() => {
    let price = SIZES_MAP[size].price;
    for (const [key, value] of Object.entries(toppings)) {
      price += value ? TOPPINGS_MAP[key].price : 0;
    }
    return price / 100;
  },[size, toppings]);

  return (
    <>
      <div style={{ width: "50%", margin: "0 auto", marginTop: 80}}>
        <h1>Buy some bubble tea!</h1>
        <FormControl>
          <FormLabel>Pick a Size</FormLabel>
          <RadioGroup row value={size}>
            {Object.values(SIZES_MAP).map(({ key, label }) => (
              <FormControlLabel
                key={key}
                control={<Radio onClick={() => setSize(key)} />}
                value={key}
                label={label}
              />
            ))}
          </RadioGroup>
          <FormLabel>Pick the sugar level</FormLabel>
          <Slider
            defaultValue={50}
            value={sugar}
            step={25}
            marks={levels}
            onChange={(e) => setSugar(e.target.value)}
          />

          <FormLabel>Pick the ice level</FormLabel>
          <Slider
            defaultValue={50}
            value={ice}
            marks={levels}
            step={25}
            onChange={(e) => setIce(e.target.value)}
          />
          <FormLabel>Select some toppings</FormLabel>
          <FormGroup row>
            {Object.values(TOPPINGS_MAP).map(({key, label}) => (
              <FormControlLabel
                key={key}
                control={
                  <Checkbox
                    value={toppings[key]}
                    onClick={() => selectToppings(key)}
                  />
                }
                label={label}
              />
            ))}
          </FormGroup>
        </FormControl>
        <h1>Total: {parseFloat(price).toFixed(2)} </h1>
        <Button size="large" variant="contained" onClick={buy}>Buy</Button>
        <PlaidLink token={token} />
      </div>
    </>
  );
};

export default Main;
